import React from 'react';
import './win.css';
 
class Win extends React.Component {
  constructor() {
    super();
  
    this.state = {
      toggle: false,
    }
  }

  componentDidMount() {
    if (this.props.win) {
      this.setState({
        toggle: true,
      });
    }
  }
  
  componentWillReceiveProps(nextProps) {
    if (nextProps.win !== this.props.win) {
      this.setState({
        toggle: nextProps.win,
      });
    }
  }

  handelClick(type) {
    this.setState({
      toggle: false,
    });
    type === 1 ? this.props.handelCloseWin() : type === 2 ? this.props.handelChallenge() : this.props.handelNextGame();
  }

  render() {
    if (!this.props.win || !this.state.toggle) return null;
  
    return (
      <div className="game-win">
        <div className="game-win-text"></div>
        <div className="game-win-time">{this.props.times}s</div>
        <div className="game-win-btns">
          <div onClick={() => this.handelClick(1)}></div>
          <div onClick={() => this.handelClick(2)}></div>
          <div onClick={() => this.handelClick(3)}></div>
        </div>
      </div>
    );
  }      
}
    
export default Win;


