import React from 'react';
import './info.css';

class Info extends React.Component {
  constructor() {
    super();

    this.state = {
      toggle: false,
      aniClass: '',
      info: '<p>9csskey: value9class itemcss</p><p>9class itemclasscssclasscssclass</p><p>valuekeykeyvalue</p><p>class item</p><p>533</p>'
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.toggleGameInfoModal && !this.state.toggle) {
      this.setState({
        toggle: true,
        aniClass: '',
      });

      setTimeout(() => {
        this.setState({
          aniClass: 'show',
        });
      }, 20);
    }
  }

  handelClose() {
    this.setState({
      aniClass: 'hide',
    });
    setTimeout(() => {
      this.setState({
        toggle: false,
      });
      this.props.closeModal();
    }, 200);
  }

  render() {
    if (!this.state.toggle) return null;

    return (
      <div className={'game-info ' + this.state.aniClass}>
        <div className="game-info-title"></div>
        <div className="game-info-content" dangerouslySetInnerHTML={{__html: this.state.info}}></div>
        <div className="game-info-btn" onClick={() => this.handelClose()}></div>
      </div>
    );
  }
}

export default Info;


